<template>
  <div style="padding: 10px">

    <!--    -->
    <div style="margin: 10px 0">
      <el-input v-model="search" placeholder="" style="width: 20%" clearable></el-input><!--clearable -->
      <el-button type="primary" style="margin-left: 5px;" @click="load"></el-button>
      <el-button type="primary" @click="add"></el-button>
    </div>

<!--    -->
    <el-table :data="tableData" border stripe style="width: calc(100% - 40px)">
      <el-table-column prop="id" label="ID" sortable/>
      <el-table-column prop="title" label="" />
      <el-table-column prop="author" label="" />
      <el-table-column prop="time" label="" />
      <el-table-column align="right" width="140px">
        <template #default="scope">
          <el-button type="primary" size="small" @click="details(scope.row)"></el-button>
          <el-button type="primary" size="small" @click="handleEdit(scope.row)" v-if="user.role === 1"></el-button>

          <el-popconfirm title=",?" @confirm="handleDelete(scope.row)">
            <template #reference>
              <el-button size="small" type="danger" v-if="user.role === 1"></el-button>
            </template>
          </el-popconfirm>

        </template>
      </el-table-column>
    </el-table>

<!--    -->
    <div style="margin: 10px 0">
      <el-pagination
          v-model:currentPage="currentPage4"
          v-model:page-size="pageSize"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="pageSize"
          :small="small"
          :disabled="disabled"
          :background="background"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>

<!--    -->
    <el-dialog v-model="dialogVisible" title="" width="60%" :before-close="handleClose">
<!--      -->
      <el-form :model="form" label-width="120px">
        <el-form-item label="">
          <el-input v-model="form.title" style="width: 50%;height: 70%"/></el-form-item>
<!--        <el-form-item label="">-->
<!--          <el-input v-model="form.content" style="width: 80%;height: 70%"/></el-form-item>-->
        <div id="toolbar-container"></div>
        <div id="editor-container"></div>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false"></el-button>
          <el-button type="primary" @click="save"></el-button>
        </span>
      </template>
    </el-dialog>

    <el-dialog v-model="vis" title="" width="50%">
      <el-card>
        <div v-html="detail.content" style="min-height: 100px"></div>
      </el-card>
    </el-dialog>
  </div>
</template>


<script>
//
import '@wangeditor/editor/dist/css/style.css'
import { createEditor, createToolbar, IEditorConfig, IDomEditor } from '@wangeditor/editor'
import request from "@/utils/request";

// 
const editorConfig= {}
editorConfig.placeholder = ''
// 
const toolbarConfig = {}

let editor

export default {
  name: 'News',
  components: {

  },
  data() {
    return {
      user:{},
      form:{},
      dialogVisible:false,
      search:'',
      currentPage4:1,
      pageSize:10,
      total:0,
      tableData:[],
      detail:{},
      vis:false
    }
  },

  created() {//
    let userStr = sessionStorage.getItem("user") || "{}"
    this.user = JSON.parse(userStr)

    //
    request.get("/user/" + this.user.id).then(res =>{
      if(res.code === '0') {
        this.user = res.data
      }
    })

    this.load()
  },

  methods:{
    details(row) {
      this.detail=row
      this.vis=true

    },
    fileUploadSuccess(res) {
      console.log(res)
      this.form.cover=res.data
    },
    load() {
      request.get("/news", {
        params:{
          pageNum:this.currentPage4,
          pageSize:this.pageSize,
          search:this.search
        }
      }).then(res =>{
        console.log(res)
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },
    add() {
      this.dialogVisible=true
      this.form={}

      //
      this.$nextTick(() =>{

        if(editor) {
          editor.destroy();
          this.createEdit();
        } else {
          this.createEdit();
        }
      })
    },
    save() {
      if(editor.getHtml()) {
        console.log(editor.getHtml())
        this.form.content = editor.getHtml() //
      }

      this.dialogVisible = false

      if(this.form.id) {//
        request.put("/news",this.form).then(res =>{
          console.log(res)

          if(res.code === '0') {
            this.$message.success("")
          } else {
            this.$message.error(res.message)
          }
        })
      } else {//
        let userStr = sessionStorage.getItem("user") || "{}"
        let user = JSON.parse(userStr)
        this.form.author = user.nickName //author

        request.post("/news",this.form).then(res =>{
          console.log(res)

          if(res.code === '0') {
            this.$message.success("")
          } else {
            this.$message.error(res.message)
          }
        })
      }
      this.load()
    },

    //
    createEdit() {
      // 
      editor = createEditor({
        html:this.form.content, //
        selector: '#editor-container',
        config: editorConfig,
        mode: 'default' //  'simple' 
      })
      // 
      toolbar = createToolbar({
        editor,
        selector: '#toolbar-container',
        config: toolbarConfig,
        mode: 'default' //  'simple' 
      })
    },
    //
    handleEdit(row) {
      this.form = JSON.parse(JSON.stringify(row))
      this.dialogVisible = true
      //
      this.$nextTick(() =>{

        if(editor) {
          editor.destroy();
          this.createEdit();
        } else {
          this.createEdit();
        }
      })
    },
    handleDelete(news) {  //
      console.log(news.id)
      request.delete("/news/"+news.id).then(res=>{

        if(res.code === '0') {
          this.$message.success("")
        } else {
          this.$message.error(res.message)
        }
      })
      this.load()
    },
    handleSizeChange() { //
      this.load()
    },
    handleCurrentChange() { //
      this.load()
    }
  }
}
</script>
